@font-face {
  font-family: "NexaBold";
  src: url(/static/media/NexaBold.c9f309b3.otf); }

@font-face {
  font-family: "NexaLight";
  src: url(/static/media/NexaLight.12108809.otf); }

@font-face {
  font-family: 'Open Sans', sans-serif;
  src: 'https://fonts.googleapis.com/css?family=Open+Sans'; }

body {
  height: 100%;
  margin: 0;
  padding: 0;
  color: #212121;
  font-family: "NexaBold";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%; }

.title {
  color: #636363;
  font-size: .8rem;
  text-transform: uppercase; }

.shadow {
  box-shadow: 0px 10px 30px 0px rgba(0, 0, 0, 0.2); }

.subtitle {
  font-family: "NexaLight";
  text-transform: uppercase;
  font-size: .8rem; }

.header {
  background: url(/static/media/header-image.74d90e53.jpg);
  background-size: cover;
  background-position: 0 -100px;
  height: 500px;
  display: flex;
  align-items: center;
  justify-content: center; }
  .header p {
    font-size: 3rem;
    color: #fafafa; }

.about {
  background: #fafafa;
  display: flex;
  text-align: left;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  width: 60%;
  z-index: 10; }
  .about .left {
    padding: 20px;
    width: 66%; }
    .about .left .about-me {
      font-size: 1.5rem; }
  .about .right {
    background: -webkit-linear-gradient(150deg, #473377 0%, #7b57cc 100%);
    background: linear-gradient(-60deg, #473377 0%, #7b57cc 100%);
    padding: 20px;
    width: 33%; }
    .about .right .about-featured {
      color: #212121;
      font-size: .8rem; }
    .about .right .about-featured-title {
      color: #fafafa;
      font-size: 1.5rem; }
    .about .right .button {
      border: #fafafa 2px solid;
      border-radius: 10px;
      color: #fafafa;
      font-size: .8rem;
      padding: 10px 0;
      text-align: center; }

.category {
  padding: 20px;
  text-align: left; }
  .category .icon {
    width: 100px; }

.outline {
  outline: #7b57cc 3px solid; }

.footer {
  display: flex;
  align-items: center;
  background: -webkit-linear-gradient(150deg, #473377 0%, #7b57cc 100%);
  background: linear-gradient(-60deg, #473377 0%, #7b57cc 100%);
  color: #fafafa;
  height: 200px;
  padding: 0 50px; }
  .footer p {
    margin: 5px 0; }
  .footer a {
    color: #fafafa; }

.App {
  text-align: left; }
  .App .about-container {
    display: flex;
    justify-content: center; }
  .App .content {
    padding: 0 100px; }
    .App .content .my-work {
      padding: 0 30px;
      text-align: left; }
    .App .content .categories-container {
      display: flex;
      justify-content: space-between; }
      .App .content .categories-container .col {
        padding: 0 30px;
        width: 33%; }
    .App .content .coming-soon {
      padding: 30px;
      font-size: 1.5rem; }

.modal {
  background: rgba(15, 19, 36, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: center; }
  .modal .modal-container {
    position: relative;
    background: white;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    padding: 50px;
    border-radius: 20px; }
    .modal .modal-container form .input-field {
      display: flex;
      align-items: center; }
      .modal .modal-container form .input-field span {
        width: 20px; }
      .modal .modal-container form .input-field input, .modal .modal-container form .input-field select {
        margin: 10px; }

.table {
  display: flex;
  flex-direction: column; }
  .table .row {
    display: flex;
    justify-content: space-between; }
    .table .row .grid-item {
      display: flex;
      align-items: center;
      justify-content: center;
      border: 1px solid #4040404d;
      padding: 20px 0 20px 0;
      font-size: 20px;
      width: 20%; }
    .table .row .gray {
      background: #b3b3b380; }
  .table .reverse {
    flex-direction: row-reverse; }

button {
  margin-top: 10px; }

.red {
  border: 3px solid #FF605F;
  background: #fff0f0;
  border-radius: 5px; }

.green {
  border: 3px solid #03D05A;
  background: #e5fff0;
  border-radius: 5px; }

.blue {
  border: 3px solid #00C1DD;
  background: #e7fcff;
  border-radius: 5px; }

@media screen and (max-width: 990px) {
  #blue {
    display: none; } }

.container {
  display: flex;
  height: 100vh;
  background: #f9faff;
  color: #303030;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif; }

button {
  width: 100px;
  height: 30px;
  border-radius: 30px;
  font-size: 12px;
  border: 2px solid #808080; }

@media screen and (max-width: 1024px) {
  .container {
    display: block; } }

@media screen and (max-width: 812px) {
  #red, #blue, #green {
    width: 100% !important;
    margin: 0px auto 20px auto !important; } }

